/***************************公共样式 *************************************/
.title {
  font-weight: bold;
  font-size: 26px;
  text-align: left;
  padding-top: 70px;
}
.ds-inline {
  display: inline;
}
.ds-block {
  display: block;
}
.ds-inline-block {
  display: inline-block;
}
.float-l {
  float: left;
}
.float-r {
  float: right;
}
.txt-c {
  text-align: center;
}
.clear {
  clear: both;
}
.in-table {
  display: inline-table;
}
.dHide {
  display: none;
}
.vHide {
  visibility:hidden;
}
